@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
html {
  box-sizing: border-box;
}

body {
  margin: 0;
  background: linear-gradient(to right, #642b73, #c6426e);
}

.App {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: 'Montserrat';
  color: white;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type='number'] {
  -moz-appearance: textfield;
}

@media screen and (max-width: 600px) {
  .app-title {
    font-size: 22px;
  }
}

.randoms-numbers-containers {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 50vh;
  width: 50vw;
  padding: 20px;
  background-color: rgb(0 0 0 / 20%);
  border-radius: 20px;
  box-shadow: 0 -1px 30px -1px rgb(0 0 0 / 50%);
  color: white;
  font-family: 'Montserrat';
}

.random-title {
  font-size: 24px;
}

.generate-form {
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;
}

.input {
  border: none;
  outline: none;
  padding: 10px;
  background-color: rgb(0 0 0 / 40%);
  color: white;
  font-family: 'Montserrat';
}

.submit-button {
  padding: 15px;
  border: none;
  outline: none;
  background: rgb(0 0 0 / 20%);
  color: white;
  cursor: pointer;
  font-family: 'Montserrat';
  transition: ease 0.2s;
}

.submit-button:hover {
  transform: scale(1.02);
}

@media screen and (max-width: 600px) {
  .randoms-numbers-containers {
    padding: 50px;
  }

  .random-title {
    font-size: 14px;
  }

  .input {
    padding: 4px;
  }

  label {
    font-size: 12px;
  }
}

